<template>
  <div :class="{ 'page-comic--sp': isSmartPhone }" class="page-comic">
    <h1 class="page-comic__heading"></h1>
    <div class="page-comic__catch page-comic-catch">
      
      <br><a class="page-comic-catch__link text-link" href="http://yukiakai.github.io/Comic-Symbols/" target="_blank"><i class="icon icon-arrow-right"></i></a>
    </div>
    <img class="page-comic__img" src="@/assets/img/comic-symbols.png" alt="" width="372" height="248">
    <h2 class="page-comic__sub-heading"></h2>
    <div class="page-comic__detail">
      
    </div>
    <h2 class="page-comic__sub-heading"></h2>
    <div class="page-comic__detail">
      Processing.js
    </div>
    <div class="page-comic__description page-comic-description">
      <h2 class="page-comic-description__heading"></h2>
      <div class="page-comic-description__detail description-detail">
         (e.g. ,  ect...) 
        <br><router-link to="/link/" class="description-detail__link text-link"><i class="icon icon-arrow-right"></i></router-link>
      </div>
      <img class="page-comic-description__img" src="@/assets/img/bebber.png" alt="bebber">
    </div>
    <router-link to="/" class="page-comic__top-link text-link"><i class="icon icon-arrow-left"></i>PAGE TOP</router-link>
  </div>
</template>

<script>
import layoutMixin from '@/mixins/layout'
export default {
  name: 'ComicSymbols',
  mixins: [layoutMixin]
}
</script>

<style lang="scss" scoped>
.page-comic {
  $this: &;
  width: 990px;
  min-height: 350px;
  padding: 30px 20px;
  margin: 0 auto;
  .icon {
    background-size: 100%;
  }
  &__heading,
  &__sub-heading {
    margin-bottom: 20px;
    font-weight: bold;
  }
  &__heading {
    font-size: 30px;
  }
  &__sub-heading {
    margin: 30px 0 10px;
    font-size: 18px;
  }
  &__img {
    display: block;
    max-width: 100%;
    height: auto;
    margin: 30px auto;
  }
  .page-comic-catch,
  .description-detail {
    &__link {
      display: inline-flex;
      align-items: center;
      margin-top: 10px;
      font-size: 18px;
      font-weight: normal;
      .icon {
        width: 18px;
        height: 18px;
        margin-left: 6px;
        background-image: url("../../../assets/svg/icon-arrow-right.svg");
      }
    }
  }
  .page-comic-catch {
    font-size: 18px;
    font-weight: bold;
  }
  .page-comic-description {
    padding: 20px;
    margin-top: 30px;
    background: #eee;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
    &__heading {
      margin-bottom: 10px;
      font-size: 18px;
      font-weight: bold;
    }
    &__img {
      display: block;
      max-width: 100%;
      margin: 30px auto 0;
    }
  }
  &__top-link {
    display: inline-flex;
    align-items: center;
    margin-top: 40px;
    .icon {
      width: 16px;
      height: 16px;
      margin-right: 6px;
      background-image: url("../../../assets/svg/icon-arrow-left.svg");
    }
  }
  &--sp {
    width: auto;
    padding: 20px;
    #{$this}__heading {
      font-size: 24px;
    }
    .page-comic-catch,
    .description-detail {
      &__link {
        font-size: 16px;
        @media screen and (max-width: 320px) {
          font-size: 14px;
        }
        .icon {
          @media screen and (max-width: 320px) {
            width: 16px;
            height: 16px;
          }
        }
      }
    }
    .page-comic-catch {
      font-size: 16px;
    }
  }
}
</style>



